/* 0. Importar familia tipografica personalizada */
@import url('https://fonts.googleapis.com/css2?family=Inter:ital,wght@0,400;0,700;0,800;1,400;1,700;1,800&display=swap');


/* 1. Importar funciones y variables de Bootstrap */
@import "../../node_modules/bootstrap/scss/functions";
@import "../../node_modules/bootstrap/scss/variables";
@import "../../node_modules/bootstrap/scss/maps";
@import "../../node_modules/bootstrap/scss/mixins";
@import "../../node_modules/bootstrap/scss/utilities";


/* 2. Redefinir las variables */
$spacer: 16px;
$grid-columns:      12;
$grid-gutter-width: 2rem;
$grid-row-columns:  6;

$grid-breakpoints: (
  xs: 0,
  sm: 512px,
  md: 768px,
  lg: 1024px,
  xl: 1280px,
  xxl: 1920px
);

$container-max-widths: (
  sm: 480px,
  md: 704px,
  lg: 960px,
  xl: 1216px,
  xxl: 1302px
);

$spacers: (
  0: 0,
  1: $spacer * .25,
  2: $spacer * .5,
  3: $spacer,
  4: $spacer * 1.5,
  5: $spacer * 2,
  6: $spacer * 2.5,
  7: $spacer * 3,
  8: $spacer * 3.5,
  9: $spacer * 4,
  10: $spacer * 4.5,
  11: $spacer * 5,
  12: $spacer * 5.5,
  13: $spacer * 6,
  14: $spacer * 6.5,
  15: $spacer * 7,
  16: $spacer * 7.5,
  17: $spacer * 8,
  18: $spacer * 8.5,
  19: $spacer * 9,
  20: $spacer * 9.5,
  21: $spacer * 10
);

$primary:  #05C9D9;
$secondary:#09203D;
$tertiary: #CB1830;
$white:    #fefefe;
$light:    #F4F4F4;
$dark:     #2b2b2b;
$black:    #030A15;
$grey:     #666666;
$footer:   #161D30;

$theme-colors: (
  "primary":  $primary,
  "secondary":$secondary,
  "tertiary": $tertiary,
  "white":    $white,
  "light":    $light,
  "dark":     $dark,
  "black":    $black,
  "grey":     $grey,
  "footer":   $footer,
);

$gradient: linear-gradient(90deg, #03445A 13.22%, #9E1A36 89.29%);

$font-sizes: (
  1: $h1-font-size,
  2: $h2-font-size,
  3: $h3-font-size,
  4: $h4-font-size,
  5: $h5-font-size,
  6: $h6-font-size
);

/* COLORS */
$body-bg: $white;
$body-color: $grey;

/* TYPOGRAPHY */
$headings-margin-bottom:      $spacer * 1.5;
$headings-font-weight:        $font-weight-bolder;
$headings-line-height:        1.2;
$headings-color:              $black;

$font-family-sans-serif:      "Inter", system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", "Noto Sans", "Liberation Sans", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
$font-family-base:            var(--#{$prefix}font-sans-serif);
$font-size-base:              1rem; 
$font-size-sm:                $font-size-base * .75;
$font-size-lg:                $font-size-base * 1.25;

$line-height-base:            1.5;
$line-height-sm:              1;
$line-height-lg:              1.5;

$h1-font-size:                $font-size-base * 5;
$h2-font-size:                $font-size-base * 4;
$h3-font-size:                $font-size-base * 2.5;
$h4-font-size:                $font-size-base * 1.5;
$h5-font-size:                $font-size-base;
$h6-font-size:                $font-size-base * .75;

$small-font-size:             $font-size-sm;

$blockquote-margin-y:         0;
$blockquote-font-size:        $font-size-base * 1.25;

$dt-font-weight:              $font-weight-normal;

$list-inline-padding:         0;

/* SHADOW */
$box-shadow:                  0px 0px $spacer 0px rgba($black, .16);
$box-shadow-sm:               0 .125rem .25rem rgba($black, .075);
$box-shadow-lg:               0 1rem 3rem rgba($black, .175);

/* NAVBAR */
$navbar-padding-y:                  $spacer;
$navbar-padding-x:                  null;

$navbar-nav-link-padding-x:         0;

$navbar-brand-padding-y:            0;
$navbar-brand-margin-end:           0;

$navbar-toggler-padding-y:          0;
$navbar-toggler-padding-x:          0;

$navbar-dark-color:                 $white;
$navbar-dark-hover-color:           $primary;
$navbar-dark-active-color:          $primary;

/* BUTTONS */
$btn-padding-y:               $spacer;
$btn-padding-x:               $spacer * 1.5;
$btn-padding-y-sm:            $spacer * 0.5;
$btn-padding-x-sm:            $spacer * 0.5;
$btn-border-width:            0;
$btn-border-radius:           $spacer * 3.5;
$btn-border-radius-sm:        $spacer * 2.5;

/* CARD */
$card-spacer-y:               $spacer * 2;
$card-spacer-x:               $spacer * 2;
$card-border-width:           0;

/* 3. Configurar las utilidades personalizadas */
$utilities: (
  "spacing": (
    property: margin,
    class: m,
    values: $spacers
  ),
  "spacing-p": (
    property: padding,
    class: p,
    values: $spacers
  ),
  "color": (
    property: color,
    class: text,
    values: $theme-colors,
  ),
  "background": (
    property: background-color,
    class: bg,
    values: $theme-colors,
  ),
  "border-color": (
    property: border-color,
    class: border,
    values: $theme-colors,
  ),
  "border-radius": (
    property: border-radius,
    class: rounded,
    values: (
      "4xl": 2rem,
    ),
  ),
  "shadow": (
    property: box-shadow,
    class: shadow,
    values: (
      null: var(--#{$prefix}box-shadow),
      sm: var(--#{$prefix}box-shadow-sm),
      lg: var(--#{$prefix}box-shadow-lg),
      none: none,
    )
  ),
  "font-size": (
    rfs: true,
    property: font-size,
    class: fs,
    values: $font-sizes
  ),
  "font-weight": (
    property: font-weight,
    class: fw,
    values: (
      normal: $font-weight-normal,
      bold: $font-weight-bold,
    )
  ),
  "line-height": (
    property: line-height,
    class: lh,
    values: (
      1: 1,
      sm: $line-height-sm,
      base: $line-height-base,
      lg: $line-height-lg,
    )
  ),
);


/* 4. Importar el resto de Bootstrap */
@import "node_modules/bootstrap/scss/utilities/api";
@import "../../node_modules/bootstrap/scss/bootstrap";


/* Agregar código personalizado */
:root {
  color-scheme: only light !important;
}

::selection,
::moz-selection {
	color: $white !important;
	background-color: $primary !important;
}

*:focus,
*.focus {
	box-shadow: none !important;
	outline: none !important;
}
*::-moz-focus-inner {
	border: 0;
	outline: none !important;
}

html {
  /*-webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased !important;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;*/
  font-size: $spacer !important;
  /*@include media-breakpoint-down(sm) {
    font-size: $spacer*0.8;
  }
  @include media-breakpoint-up(rt15) {
    font-size: $spacer*1.1;
  }
  @include media-breakpoint-up(uhd) {
    font-size: $spacer*1.2;
  }
  @include media-breakpoint-up(4k) {
    font-size: $spacer*1.2;
  }
  @include media-breakpoint-up(8k) {
    font-size: $spacer*1.3;
  }*/
}

body {
  background-color: $white !important;
}

@media (prefers-color-scheme: dark) {
  body {
      background-color: $white !important;
  }
}

/* BOOTSTRAP */
.navbar {
  .nav-item {
    transition: $transition-base;
    .nav-link {
      margin: 0 $spacer * .5;
      transition: $transition-base;
      &.active {
        color: $primary !important;
      }
    }
  }
  .navbar-toggler {
    border: 0;
    &[aria-expanded=true] {
      display: none;
    }
  }
  .navbar-collapse.show {
    min-height: calc(100vh - 5.5rem);
    padding: $spacer * 2 0 0 0;
    .nav-item {
      margin: $spacer * .5 0;
    }
    .nav-link {
      font-size: $h4-font-size;
      font-weight: $font-weight-bolder;
      padding: $spacer * .5 0;
      margin: 0;
    }
  }
}

.btn {
  color: $white;
  display: inline-flex;
  &:hover {
    will-change: transform, background-color;
    color: $white !important;
    background-color: lighten($primary, 8%);
  }
}

a[class*="text-"] {
  text-decoration: none;
  position: relative;
  transition: $transition-base;
  &::after,
  &:after {
    content: '';
    display: block;
    width: 0%;
    position: absolute;
    top: $spacer;
    bottom: 0;
    left: 0;
    border-bottom: 1px solid $primary;
    transition: width .4s ease;
  }
  &:hover {
    color: $primary !important;

    &::after,
    &:after {
      content: '';
      display: block;
      width: 100%;
      position: absolute;
      top: $spacer * 1.5;
      bottom: 0;
      left: 0;
      border-bottom: 1px solid lighten($primary, 10%);
      transition: width .4s ease;
    }
  }
}

.above-the-fold {
  background-color: $secondary;
  text-align: center;
  min-height: calc(50vh - 80px);
  display: flex;
  justify-content: center;
  align-items: center;
}

.card {
  --bs-card-border-radius: 0px 2rem 0px 0px;
  position: relative;
  &::after,
  &:after {
    content: '';
    background-color: $primary;
    display: block;
    width: $spacer * 10;
    height: $spacer * .25;
    position: absolute;
    bottom: 0;
    left: 0;
  }
}

.inverti__img {
  margin-left: -4rem;
}

.roundend-top-left {
  border-top-left-radius: $spacer * 10;
}

.roundend-top-right {
  border-top-right-radius: $spacer * 10;
}

@media (max-width: 1024px) {
  .navbar {
    .navbar-collapse.show {
      min-height: calc(100vh - 4rem);
    }
  }

  .roundend-top-left {
    border-top-left-radius: 0 !important;
  }

  .roundend-top-right {
    border-top-right-radius: 0 !important;
  }
  
  .inverti__img {
    margin: 0;
  }
}

.blockquote {
  color: $secondary;
  font-weight: $font-weight-bold;
  display: flex;
  padding-left: 4rem;
}

footer .nav-link {
  color: $white !important;
  padding: 0;
  &:hover, &:focus {
    color: $primary !important;
  }
}

button[aria-label="Agendar una cita"],
.qxCTlb {
  background-color: $primary !important;
  color: $white !important;
  font-size: $font-size-base !important;
  font-weight: $font-weight-normal !important;
  font-family: $font-family-sans-serif !important;
  line-height: $line-height-base !important;
  border-radius: $btn-border-radius !important;
  border: none !important;
  padding: $btn-padding-y $btn-padding-x !important;
  cursor: pointer !important;
}

button[aria-label="Agendar una cita"]:hover,
.qxCTlb:hover {
  color: $white !important;
  background-color: lighten($primary, 8%) !important;
}